h3 {
    margin-block-start: 1em;
    margin-block-end: 0;
}

.single-page {
    margin: auto;
    width: 700px;
    background-color: #FFF;
    border-radius: 1em;
    padding: 0.25em 1em 1em 1em;
}

@media screen and (max-width: 750px) {
    .single-page {
        width: 90%;
    }
}